import { Injectable } from '@angular/core';
import { EmailProperties, sp } from '@pnp/sp';
import { from, Observable, defer } from 'rxjs';
import { flatMap, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MailService {

  constructor() { }

  public sendEmail(sender: string, recipients: string[], subject: string, body: string): Observable<any>  {
    return from(sp.utility.sendEmail({
      From: sender,
      To: recipients,
      Subject: subject,
      Body: body
    }));
  }

  public sendEmailByCurrentUser(recipients: string[], subject: string, body: string): Observable<any> {
    const o = new Observable((observer) => {
        sp.utility.getCurrentUserEmailAddresses().then(sender => {
          this.sendEmail(sender, recipients, subject, body).subscribe(() => {
            observer.next();
            observer.complete();
          });
        });
    });
    o.subscribe();
    return o;
  }
}
