import { Injectable } from '@angular/core';
import { ICachingOptions } from '@pnp/odata';
import { ListItemsService } from '../../../services/list-items-service';
import { Rooms } from './rooms';

@Injectable({
  providedIn: 'root'
})
export class RoomsService extends ListItemsService<Rooms> {

  constructor() {
    super();
  }

  getListRelativePath(): string {
    return '/Lists/slRooms';
  }

  getListTitle(): string {
    return 'Переговорные';
  }

  getSelect(): string {
    return 'Id, Title, slVideoStaff, slProjector, slOfficeLookup/Title, slOfficeLookup/Id';
  }

  getExpand(): string {
    return 'slOfficeLookup';
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return null;
  }

  getCachingOptions(): ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): Rooms {
    const rooms: Rooms = {
      id: item.Id,
      title: item.Title,
      videoStaff: (item.slVideoStaff === 'Да') ? true : false,
      projector: (item.slProjector === 'Да') ? true : false
    };
    if (item.slOfficeLookup) {
      rooms.officeLookupId = item.slOfficeLookup.Id;
      rooms.officeLookupTitle = item.slOfficeLookup.Title;
    }
    return rooms;
  }

  convertEntityToListItem(entity: Rooms) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Rooms[] {
    // return [{ id: 1, title: '№301' }, { id: 2, title: '№302' }, { id: 3, title: '№303' }];
    return null;
  }

  isMock(): boolean {
    return false;
  }
}
