import { Injectable } from '@angular/core';
import { ICachingOptions } from '@pnp/odata';
import { ListItemsService } from '../../../services/list-items-service';
import { Offices } from './offices';

@Injectable({
  providedIn: 'root'
})
export class OfficesService extends ListItemsService<Offices> {

  constructor() {
    super();
  }

  getListRelativePath(): string {
    return '/Lists/slOffices';
  }

  getListTitle(): string {
    return 'Офисы';
  }

  getSelect(): string {
    return 'Id,Title';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return null;
  }

  getCachingOptions(): ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): Offices {
    const offices: Offices = {
      id: item.Id,
      title: item.Title
    }
    return offices;
  }

  convertEntityToListItem(entity: Offices) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Offices[] {
    return [{ id: 1, title: 'ЛГОК' }, { id: 2, title: 'МГОК' }, { id: 3, title: 'ОЭМК' }];
  }

  isMock(): boolean {
    return false;
  }
}
