import { Component, OnInit } from '@angular/core';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';

import { Offices } from 'src/app/booking/services/offices/offices';
import { OfficesService } from 'src/app/booking/services/offices/offices.service';
import { Reservations } from 'src/app/booking/services/reservations/reservations';
import { ReservationsService } from 'src/app/booking/services/reservations/reservations.service';
import { Rooms } from 'src/app/booking/services/rooms/rooms';
import { RoomsService } from 'src/app/booking/services/rooms/rooms.service';
import { MeetingTypes } from 'src/app/booking/services/meeting-types/meeting-types';
import { MeetingTypesService } from 'src/app/booking/services/meeting-types/meeting-types.service';
import { ReservationsMailService } from 'src/app/booking/services/reservations-mail/reservations-mail.service';
import { allResolved } from 'q';
import * as _ from 'lodash';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';

export interface GroupedReservations {
  date: string;
  list: Reservations[];
}

@Component({
  selector: 'app-meeting-room-reservation-page',
  templateUrl: './meeting-room-reservation-page.component.html',
  styleUrls: ['./meeting-room-reservation-page.component.scss']
})
export class MeetingRoomReservationPageComponent implements OnInit {

  private webId: string = undefined;
  public offices: Offices[] = [];
  public rooms: Rooms[] = [];
  public groupedReservations: GroupedReservations[] = [];
  public editableReservation = [];
  public editableReservationDate: string;
  public meetingTypes: MeetingTypes[] = [];
  public participantsString: String = '';
  public cell: GroupedReservations;
  public editableReservationIndex: number;

  public currentUser = this.sharepointService.getCurrentUserContext().userLoginName;
  public newEventDate = undefined;
  public newEventRoomId: Number = undefined;

  public isCreateItem = true;
  public isDublicateItem = true;
  public masterIsOpen = false;
  public modalIsOpen = false;
  public showInfo = false;
  public isFirstStage = true;
  public showAlert = false;
  public isDeleteItem: Boolean = undefined;


  cities = ['Москва', 'Владимир', 'Нижний Новогород', 'Урюпинск'];
  durations = ['30 минут', '1 час', '1,5 часа', '2 часа', '2,5 часа', '3 часа', '4 часа', '5 часов'];
  equipment = [
    {
      icon: 'assets/icons/icon-mrr-master-phone.svg',
      title: 'Телефон'
    },
    {
      icon: 'assets/icons/icon-mrr-master-computer.svg',
      title: 'Компьютер'
    },
    {
      icon: 'assets/icons/icon-mrr-master-projector.svg',
      title: 'Проектор'
    },
    {
      icon: 'assets/icons/icon-mrr-master-wi-fi.svg',
      title: 'Wi-Fi'
    }
  ];

  displayedDay = moment();

  directionFilterByDay = {
    yesterday: 0, today: 1, tomorrow: 2
  };

  officeId = undefined;

  public openMasterModal() {
    this.masterIsOpen = true;
  }

  public createEvent(creationDate, startTime, room, cell) {
    this.isCreateItem = true;
    this.participantsString = null;
    this.modalIsOpen = true;
    creationDate = creationDate.format('DD.MM.YYYY');
    this.newEventDate = moment(creationDate + ':' + startTime, 'DD.MM.YYYY:HH:mm').toISOString();
    this.newEventRoomId = room;
    this.cell = cell;
    this.isDublicateItem = false;
  }

  public openEditorModal(reservation, cell, index) {
    reservation['participants'].forEach(element => {
      this.participantsString += (this.participantsString.length === 0) ? element.UserName : ';' + element.UserName;
    });
    this.editableReservation = JSON.parse(JSON.stringify(reservation));
    this.editableReservationDate = moment(reservation.eventDate).format('DD.MM.YYYY');
    this.isCreateItem = false;
    this.modalIsOpen = true;
    this.cell = cell;
    this.editableReservationIndex = index;
  }

  public openDublicateModal(reservation) {
    reservation['participants'].forEach(element => {
      this.participantsString += (this.participantsString.length === 0) ? element.UserName : ';' + element.UserName;
    });
    this.editableReservation = JSON.parse(JSON.stringify(reservation));
    this.editableReservationDate = moment(reservation.eventDate).format('DD.MM.YYYY');
    this.editableReservationDate = null;
    this.editableReservation['eventDate'] = null;
    this.editableReservation['endDate'] = null;
    this.editableReservation['roomLookupId'] = null;
    this.isCreateItem = true;
    this.isDublicateItem = true;
    this.modalIsOpen = true;
  }

  public closeModal() {
    this.modalIsOpen = false;
    this.editableReservation = [];
    this.participantsString = '';
  }

  public resetMaster() {
    this.masterIsOpen = false;
  }

  constructor(
    private sharepointService: SharepointService,
    private sidebarService: SidebarService,
    private officesService: OfficesService,
    private route: ActivatedRoute,
    private roomsService: RoomsService,
    private reservationsService: ReservationsService,
    private meetingTypesService: MeetingTypesService,
    private reservationsMailService: ReservationsMailService) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;

    this.officesService.getItems({webId: this.webId}).subscribe(offices => {
      this.offices = offices;
    });

    this.roomsService.getItems({webId: this.webId}).subscribe(rooms => {
      this.rooms = rooms;
    });

    this.meetingTypesService.getItems({webId: this.webId}).subscribe(meetingTypes => {
      this.meetingTypes = meetingTypes;
    });

    const today = this.displayedDay.startOf('day').toISOString();
    const tomorrow = this.displayedDay.endOf('day').toISOString();
    this.getGroupedReservationsByDay(today, tomorrow, groupedReservations => {
      this.groupedReservations = groupedReservations;
    });
  }

  public filterByOfficeId(id?: number): void {
    this.officeId = id;
  }

  public filterByToday(direction: number) {
    let dateStart: string;
    let dateEnd: string;

    switch (direction) {
      case this.directionFilterByDay.yesterday: {
        dateEnd = this.displayedDay.startOf('day').toISOString();
        dateStart = this.displayedDay.subtract(1, 'days').startOf('day').toISOString();
        break;
      }
      case this.directionFilterByDay.today: {
        dateStart = moment().startOf('day').toISOString();
        dateEnd = moment().add(1, 'days').startOf('day').toISOString();
        this.displayedDay = moment();
        break;
      }
      default: {
        dateStart = this.displayedDay.add(1, 'days').startOf('day').toISOString();
        dateEnd = this.displayedDay.endOf('day').toISOString();
        break;
      }
   }
    this.getGroupedReservationsByDay(dateStart, dateEnd, groupedReservations => {
      this.groupedReservations = groupedReservations;
      this.filterByOfficeId(this.officeId);
    });
  }

  public getGroupedReservationsByDay(dateStart: string, dateEnd: string, returnReservations: (groupedReservations: GroupedReservations[])
   => void) {
    this.reservationsService.getItems({
      filter: `EventDate ge datetime'${dateStart}' and EventDate lt'${dateEnd}'`,
      webId: this.webId
    }).subscribe((reservations: Reservations[] ) => {

      const groupedByDate: object = _.groupBy(reservations, reservation => this.getEventTime(reservation.eventDate));
      const timeLine = ['08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30', '14:00',
      '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30'];

      const allReservations = [];
      timeLine.forEach(time => {
        allReservations.push(<GroupedReservations>{
          date: time,
          list: groupedByDate[time]
        });
      });
      returnReservations(allReservations);
    });
  }

  public getEventTime(date: string): string {
    return moment(date).format('HH:mm');
  }

  public getDisplayedDate(): string {
    return this.displayedDay.format('DD.MM.YYYY');
  }

  remove(item: any) {
    this.reservationsService.deleteItem(item)
      .subscribe(() => {
        this.groupedReservations.forEach(time => {
          if (time.list !== undefined) {
            time.list = time.list.filter(i => i['id'] !== item['id']);
          }
        });
        this.reservationsMailService.removeNotify(item);
      });
  }
}
