import { Component, OnInit } from '@angular/core';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';

@Component({
  selector: 'app-booking-master-page',
  templateUrl: './booking-master-page.component.html',
  styleUrls: ['./booking-master-page.component.scss']
})
export class BookingMasterPageComponent implements OnInit {

  public pickedRoom = null;
  public isFirstStep = true;

  public currentStep(value) {
    this.isFirstStep = value;
  }

  public selectedRoom(value) {
    this.pickedRoom = value;
  }

  constructor(
    private sidebarService: SidebarService) {
      this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
    }

  ngOnInit() {
  }

}
