import { Injectable } from '@angular/core';
import { ICachingOptions } from '@pnp/odata';
import { ListItemsService } from '../../../services/list-items-service';
import { MeetingTypes } from './meeting-types';

@Injectable({
  providedIn: 'root'
})
export class MeetingTypesService extends ListItemsService<MeetingTypes> {

  constructor() {
    super();
  }

  getListRelativePath(): string {
    return '/Lists/slMeetingTypes';
  }

  getListTitle(): string {
    return 'Типы встреч';
  }

  getSelect(): string {
    return 'Id, Title';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return null;
  }

  getCachingOptions(): ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): MeetingTypes {
    const meetingTypes: MeetingTypes = {
      id: item.Id,
      title: item.Title
    }
    return meetingTypes;
  }

  convertEntityToListItem(entity: MeetingTypes) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): MeetingTypes[] {
    return [{ id: 1, title: 'Собеседование' }, { id: 2, title: 'Встреча' }, { id: 3, title: 'Совещание' }];
  }

  isMock(): boolean {
    return false;
  }
}
