import { Component, OnInit, Input, Output, EventEmitter, ElementRef} from '@angular/core';
import { ReservationsService } from 'src/app/booking/services/reservations/reservations.service';
import { Reservations } from 'src/app/booking/services/reservations/reservations';
import { ReservationsMailService } from 'src/app/booking/services/reservations-mail/reservations-mail.service';
import * as moment from 'moment';
import * as _ from 'lodash';
import { SiteUserProps } from '@pnp/sp';
import { ActivatedRoute } from '@angular/router';

export interface GroupedReservations {
  date: string;
  list: Reservations[];
}

@Component({
  selector: 'app-booking-modal-form',
  templateUrl: './booking-modal-form.component.html',
  styleUrls: ['./booking-modal-form.component.scss']
})
export class BookingModalFormComponent implements OnInit {

  public user: SiteUserProps = null;
  private webId: string = undefined;

  @Output() close: EventEmitter<null> = new EventEmitter();

  @Input() isCreateItem = false;
  @Input() modalIsOpen = false;
  @Input() newEventDate: string;
  @Input() newEventRoomId: number;
  @Input() currentUser: string;
  @Input() groupedReservations: any;
  @Input() editableReservation: Reservations;
  @Input() editableReservationDate: string;
  @Input() rooms: [];
  @Input() meetingTypes: [];
  @Input() participantsString: string;
  @Input() cell: GroupedReservations;
  @Input() editableReservationIndex: number;
  @Input() isDublicateItem: boolean;

  constructor(
    private reservationsService: ReservationsService,
    private reservationsMailService: ReservationsMailService,
    private route: ActivatedRoute
  ) { }
  timeLine = ['08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30',
  '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30', '20:00'];

  booleanSelector = ['Да', 'Нет'];
  selectStart = '';
  selectEnd = '';

  public formInValid: Boolean = true;

  public convertDateToTime(date: string): string {
    return moment(date).format('HH:mm');
  }

  public convertDateToDay(date: string): string {
    return moment(date).format('DD.MM.YYYY');
  }

  public closeModal() {
    this.close.emit();
    this.editableReservation = null;
    this.participantsString = '';
  }

  public changeOfficeId() {
    if (this.editableReservation) {
      this.editableReservation.officeLookupId = this.rooms.filter(room => {
        return room['id'] === Number(this.editableReservation.roomLookupId);
      }).map(room => room['officeLookupId']).shift();
    }
  }

  public changeRoom(date) {
    this.changeStartTime(date, {target: { value: this.selectStart}});
    this.changeEndTime(date, {target: { value: this.selectEnd}});
  }

  public changeDay(date) {
    const dateStart = moment(date, 'DD.MM.YYYY').startOf('day').toISOString();
    const dateEnd = moment(date, 'DD.MM.YYYY').endOf('day').toISOString();
    this.groupedReservations = [];
    this.reservationsService.getItems({
      filter: `EventDate ge datetime'${dateStart}' and EventDate lt'${dateEnd}'`
    }).subscribe((reservations: Reservations[] ) => {

      const groupedByDate: object = _.groupBy(reservations, reservation => this.convertDateToTime(reservation.eventDate));
      const timeLine = ['08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30', '14:00',
      '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30'];

      timeLine.forEach(time => {
        this.groupedReservations.push(<GroupedReservations>{
          date: time,
          list: groupedByDate[time]
        });
      });
      this.changeStartTime(date, {target: { value: this.selectStart}});
      this.changeEndTime(date, {target: { value: this.selectEnd}});
    });
  }

  public changeStartTime(date: string, newValue) {
    const proposedTime = this.convertDateToTime(this.editableReservation.eventDate);
    this.editableReservation.eventDate = moment(date + ':' + newValue.target.value, 'DD.MM.YYYY:HH:mm').toISOString();
    let valid = true;
    this.groupedReservations.forEach(data => {
      const formatedReservationStart = moment(this.editableReservation.eventDate).format('X');
      const formatedReservationEnd = moment(this.editableReservation.endDate).format('X');
      if (data.list !== undefined) {
        for (const reservation of data.list) {
          const tempEventStart = moment(reservation.eventDate).format('X'), tempEventEnd = moment(reservation.endDate).format('X'),
          tempRoomId = reservation.roomLookupId;
          if  ((formatedReservationStart >= tempEventStart) &&
              (formatedReservationStart < tempEventEnd) &&
              (Number(this.editableReservation.roomLookupId) === tempRoomId)) {
            if (this.editableReservation.id !== reservation.id) {
              valid = false;
              newValue.target.value = proposedTime;
            }
          } else if ((formatedReservationStart < tempEventStart) &&
                    (formatedReservationStart < tempEventEnd) &&
                    (formatedReservationEnd > tempEventStart) &&
                    (formatedReservationEnd > tempEventEnd) &&
                    (Number(this.editableReservation.roomLookupId) === tempRoomId)) {
            if (this.editableReservation.id !== reservation.id) {
              valid = false;
              newValue.target.value = proposedTime;
              break;
            }
          }
        }
      }
    });
    this.editableReservation.eventDate = moment(date + ':' + newValue.target.value, 'DD.MM.YYYY:HH:mm').toISOString();
    this.editableReservation.eventHeight = this.getEventHeight(this.editableReservation.eventDate, this.editableReservation.endDate);

    if (!valid) {
      alert('Выбранное время занято');
    }
  }

  public changeEndTime(date: string, newValue) {
    const proposedTime = this.convertDateToTime(this.editableReservation.endDate);
    this.editableReservation.endDate = moment(date + ':' + newValue.target.value, 'DD.MM.YYYY:HH:mm').toISOString();
    let valid = true;
    this.groupedReservations.forEach(data => {
      const formatedReservationStart = moment(this.editableReservation.eventDate).format('X');
      const formatedReservationEnd = moment(this.editableReservation.endDate).format('X');
      if (data.list !== undefined) {
        for (const reservation of data.list) {
          const tempEventStart = moment(reservation.eventDate).format('X'), tempEventEnd = moment(reservation.endDate).format('X'),
          tempRoomId = reservation.roomLookupId;
          if  ((formatedReservationEnd > tempEventStart) &&
              (formatedReservationEnd <= tempEventEnd) &&
              (Number(this.editableReservation.roomLookupId) === tempRoomId)) {
            if (this.editableReservation.id !== reservation.id) {
              valid = false;
              newValue.target.value = proposedTime;
            }
          } else if ((formatedReservationStart < tempEventStart) &&
                    (formatedReservationStart < tempEventEnd) &&
                    (formatedReservationEnd > tempEventStart) &&
                    (formatedReservationEnd > tempEventEnd) &&
                    (Number(this.editableReservation.roomLookupId) === tempRoomId)) {
            if (this.editableReservation.id !== reservation.id) {
              valid = false;
              newValue.target.value = proposedTime;
              break;
            }
          }
        }
      }
    });
    this.editableReservation.endDate = moment(date + ':' + newValue.target.value, 'DD.MM.YYYY:HH:mm').toISOString();
    this.editableReservation.eventHeight = this.getEventHeight(this.editableReservation.eventDate, this.editableReservation.endDate);

    if (!valid) {
      alert('Выбранное время занято');
    }
  }

  public changeResponsible(users) {
    if (this.editableReservation) {
      this.editableReservation.responsibleId = users && users.length > 0 ? users[0].Id : null;
    }
  }

  public changeInitiator(users) {
    if (this.editableReservation) {
      this.editableReservation.initiatorId = users && users.length > 0 ? users[0].Id : null;
    }
  }

  public changeParticipants(users) {
    if (this.editableReservation) {
      this.editableReservation.participants = users.map(user => {
        return {
          Id : user.Id,
          Title: user.Title,
          UserName: user.LoginName,
          EMail: user.Email
        };
      });
    }
  }

  public getEventHeight(eventDate: string, endDate: string): number {
    const timeStart = moment(eventDate);
    const timeEnd = moment(endDate);
    const eventHeight = timeEnd.diff(timeStart, 'hours', true);
    return eventHeight / .5;
  }

  public updateReservation(item: Reservations) {
    const timeStart = moment(item.eventDate).format('X');
    const timeEnd = moment(item.endDate).format('X');
    if (timeStart >= timeEnd) {
      alert('Время окончания не может быть меньше или равняться времени начала');
    } else {
      this.reservationsService.updateItem({
        id: item.id,
        title: item.title,
        participants: {'results' : item.participants.map(user => user.Id)} ,
        videoStaff: item.videoStaff,
        projector: item.projector,
        description: item.description,
        initiator: item.initiator,
        initiatorId: item.initiatorId,
        initiatorLogin: item.initiatorLogin,
        responsible: item.responsible,
        responsibleId: item.responsibleId,
        responsibleLogin: item.responsibleLogin,
        count: item.count,
        eventDate: item.eventDate,
        endDate: item.endDate,
        companyLookupId: item.companyLookupId,
        officeLookupId: item.officeLookupId,
        roomLookupId: item.roomLookupId,
        meetingTypeLookupId: item.meetingTypeLookupId
      }, this.webId).subscribe((itemResived) => {
        const oldReservation = this.cell.list[this.editableReservationIndex];
        if (this.convertDateToTime(item.eventDate) !== this.convertDateToTime(oldReservation.eventDate)) {
          this.reservationsMailService.editTimeNotify(item, oldReservation.eventDate);
        } else if (item.eventDate !== oldReservation.eventDate) {
          this.reservationsMailService.editDateNotify(item, oldReservation.eventDate);
        }
        if (this.convertDateToDay(oldReservation.eventDate) !== this.convertDateToDay(this.editableReservation.eventDate)) {
          this.cell.list.splice(this.editableReservationIndex, 1);
        } else {
          this.cell.list[this.editableReservationIndex] = this.editableReservation;
        }
        this.closeModal();
      });
    }
  }

  public createReservation(item: Reservations) {
    const timeStart = moment(item.eventDate).format('X');
    const timeEnd = moment(item.endDate).format('X');
    if (timeStart >= timeEnd) {
      alert('Время окончания не может быть меньше или равняться времени начала');
    } else {
      if (((item.title === undefined) ? '' : item.title).length <= 0) {
        alert('Поле "Тема" обязательно к заполнению');
      } else if (item.meetingTypeLookupId == null) {
        alert('Поле "Тип встречи" обязательно к заполнению');
      } else if (item.initiatorId == null) {
        alert('Поле "Инициатор" обязательно к заполнению');
      } else if (item.responsibleId == null) {
        alert('Поле "Ответственный" обязательно к заполнению');
      } else if (item.participants == null) {
        alert('Поле "Участники" обязательно к заполнению');
      } else if (item.count == null) {
        alert('Поле "Количество участников" обязательно к заполнению');
      } else if (item.videoStaff == null) {
        alert('Поле "Наличие оборудования для видеоконференции" обязательно к заполнению');
      } else if (item.projector == null) {
        alert('Поле "Наличие проектора/ТВ" обязательно к заполнению');
      } else {
        this.reservationsService.createItem({
          id: undefined,
          title: item.title,
          participants: {'results' : item.participants.map(user => user.Id)},
          videoStaff: item.videoStaff,
          projector: item.projector,
          description: item.description,
          initiator: item.initiator,
          initiatorId: item.initiatorId,
          initiatorLogin: item.initiatorLogin,
          responsible: item.responsible,
          responsibleId: item.responsibleId,
          responsibleLogin: item.responsibleLogin,
          count: item.count,
          eventDate: item.eventDate,
          endDate: item.endDate,
          companyLookupId: item.companyLookupId,
          officeLookupId: item.officeLookupId,
          roomLookupId: item.roomLookupId,
          meetingTypeLookupId: item.meetingTypeLookupId
        }, this.webId).subscribe((itemResived) => {
          this.editableReservation.id = itemResived.id;
          if (this.cell.list === undefined) {
            this.cell.list = [this.editableReservation];
          } else {
            this.cell.list.push(this.editableReservation);
          }
          this.reservationsMailService.createNotify(item);
          this.closeModal();
        });
      }
    }
  }

  public dublicateReservation(item: Reservations) {
    const timeStart = moment(item.eventDate).format('X');
    const timeEnd = moment(item.endDate).format('X');
    if (timeStart >= timeEnd) {
      alert('Время окончания не может быть меньше или равняться времени начала');
    } else {
      if (((item.title === undefined) ? '' : item.title).length <= 0) {
        alert('Поле "Тема" обязательно к заполнению');
      } else if (item.meetingTypeLookupId == null) {
        alert('Поле "Тип встречи" обязательно к заполнению');
      } else if (item.initiatorId == null) {
        alert('Поле "Инициатор" обязательно к заполнению');
      } else if (item.responsibleId == null) {
        alert('Поле "Ответственный" обязательно к заполнению');
      } else if (item.participants == null) {
        alert('Поле "Участники" обязательно к заполнению');
      } else if (item.count == null) {
        alert('Поле "Количество участников" обязательно к заполнению');
      } else if (item.videoStaff == null) {
        alert('Поле "Наличие оборудования для видеоконференции" обязательно к заполнению');
      } else if (item.projector == null) {
        alert('Поле "Наличие проектора/ТВ" обязательно к заполнению');
      } else {
        this.reservationsService.createItem({
          id: undefined,
          title: item.title,
          participants: {'results' : item.participants.map(user => user.Id)},
          videoStaff: item.videoStaff,
          projector: item.projector,
          description: item.description,
          initiator: item.initiator,
          initiatorId: item.initiatorId,
          initiatorLogin: item.initiatorLogin,
          responsible: item.responsible,
          responsibleId: item.responsibleId,
          responsibleLogin: item.responsibleLogin,
          count: item.count,
          eventDate: item.eventDate,
          endDate: item.endDate,
          companyLookupId: item.companyLookupId,
          officeLookupId: item.officeLookupId,
          roomLookupId: item.roomLookupId,
          meetingTypeLookupId: item.meetingTypeLookupId
        }, this.webId).subscribe((itemResived) => {
          this.editableReservation.id = itemResived.id;
          this.reservationsMailService.createNotify(item);
          this.closeModal();
        });
      }
    }
  }

  ngOnInit() {
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;
    if (this.isCreateItem === true) {
      this.editableReservation.initiatorLogin = this.currentUser;
      this.editableReservation.roomLookupId = this.newEventRoomId;
      this.editableReservation.eventDate = this.newEventDate;
      this.editableReservation.endDate = moment(this.editableReservation.eventDate).add(0.5, 'hours').toISOString();
      this.editableReservation.eventHeight = this.getEventHeight(this.editableReservation.eventDate, this.editableReservation.endDate);
      if (this.isDublicateItem === false) {
        this.editableReservationDate = moment(this.newEventDate).format('DD.MM.YYYY');
      }
    }
    this.selectStart = this.convertDateToTime(this.editableReservation.eventDate);
    this.selectEnd = this.convertDateToTime(this.editableReservation.endDate);

  }

}
