import { Injectable } from '@angular/core';
import { MailService } from 'src/app/shared/services/mail/mail.service';
import { Rooms } from 'src/app/booking/services/rooms/rooms';
import { RoomsService } from 'src/app/booking/services/rooms/rooms.service';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ReservationsMailService {

  public rooms: Rooms[] = [];

  constructor(
    private roomsService: RoomsService,
    private mailService: MailService) {
      this.roomsService.getItems().subscribe(rooms => {
        this.rooms = rooms;
      });
    }

  public createNotify(item: any) {
    const eventDate = moment(item.eventDate).format('DD.MM.YYYY' + ' в ' + 'HH:mm');
    let roomTitle = '';
    this.rooms.forEach(room => {
      if (room.id === item.roomLookupId) {
        roomTitle = room.title;
      }
    });
    const subject = 'Встреча';
    const body = 'Встреча запланирована на ' + eventDate + ', переговорная ' + roomTitle;
    let recipients = '';
    item['participants'].forEach(element => {
      recipients += (recipients.length === 0) ? element.EMail : ';' + element.EMail;
    });
    const recipientsArr = recipients.split(';');
    console.log('Создал');
    console.log(recipientsArr);
    console.log(subject);
    console.log(body);
    this.mailService.sendEmailByCurrentUser(recipientsArr, subject, body);
  }

  public editTimeNotify(item: any, oldEventDate) {
    const eventDate = moment(item.eventDate).format('HH:mm');
    const oldDate = moment(oldEventDate).format('DD.MM.YYYY' + ' в ' + 'HH:mm');
    const subject = 'Время встречи изменено';
    const body = 'Встреча, запланированная на ' + oldDate + ' перенесена на ' + eventDate;
    let recipients = '';
    item['participants'].forEach(element => {
      recipients += (recipients.length === 0) ? element.EMail : ';' + element.EMail;
    });
    const recipientsArr = recipients.split(';');
    console.log('Изменил');
    console.log(recipientsArr);
    console.log(subject);
    console.log(body);
    this.mailService.sendEmailByCurrentUser(recipientsArr, subject, body);
  }

  public editDateNotify(item: any, oldEventDate) {
    const eventDate = moment(item.eventDate).format('DD.MM.YYYY' + ' в ' + 'HH:mm');
    const oldDate = moment(oldEventDate).format('DD.MM.YYYY' + ' в ' + 'HH:mm');
    const subject = 'Время и дата встречи изменено';
    const body = 'Встреча, запланированная на ' + oldDate + ' перенесена на ' + eventDate;
    let recipients = '';
    item['participants'].forEach(element => {
      recipients += (recipients.length === 0) ? element.EMail : ';' + element.EMail;
    });
    const recipientsArr = recipients.split(';');
    console.log('Изменил');
    console.log(recipientsArr);
    console.log(subject);
    console.log(body);
    this.mailService.sendEmailByCurrentUser(recipientsArr, subject, body);
  }

  public removeNotify(item: any) {
    const eventDate = moment(item.eventDate).format('DD.MM.YYYY' + ' в ' + 'HH:mm');
    const subject = 'Встреча отменена';
    const body = 'Запланированная встреча на ' + eventDate + ', переговорная ' + item.roomLookupTitle + ' отменена';
    let recipients = '';
    item['participants'].forEach(element => {
      recipients += (recipients.length === 0) ? element.EMail : ';' + element.EMail;
    });
    const recipientsArr = recipients.split(';');
    console.log('Удалил');
    console.log(recipientsArr);
    console.log(subject);
    console.log(body);
    this.mailService.sendEmailByCurrentUser(recipientsArr, subject, body);
  }

}
