import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-booking-master-step-one',
  templateUrl: './booking-master-step-one.component.html',
  styleUrls: ['./booking-master-step-one.component.scss']
})

export class BookingMasterStepOneComponent implements OnInit {

  @Input() pickedRoom: any;
  @Input() isFirstStep: boolean;
  @Output() changeStep = new EventEmitter<boolean>();
  @Output() selectRoom = new EventEmitter<any>();

  public freeRooms = [
    {
      title: '№301',
      officeLookupTitle: 'Москва',
      places: 8
    },
    {
      title: '№302',
      officeLookupTitle: 'Москва',
      places: 8
    },
    {
      title: '№303',
      officeLookupTitle: 'Москва',
      places: 14
    },
    {
      title: '№304',
      officeLookupTitle: 'Москва',
      places: 14
    },
    {
      title: '№305',
      officeLookupTitle: 'Москва',
      places: 20
    },
    {
      title: '№306',
      officeLookupTitle: 'Москва',
      places: 20
    }
  ];

  public pickRoom(room) {
    this.pickedRoom = room;
    this.isFirstStep = !this.isFirstStep;
    this.selectRoom.emit(this.pickedRoom);
    this.changeStep.emit(this.isFirstStep);
  }

  constructor() { }

  ngOnInit() {
  }

}
