import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-booking-master-step-two',
  templateUrl: './booking-master-step-two.component.html',
  styleUrls: ['./booking-master-step-two.component.scss']
})
export class BookingMasterStepTwoComponent implements OnInit {

  @Input() pickedRoom: any;
  @Input() isFirstStep: boolean;
  @Output() changeStep = new EventEmitter<boolean>();

  cancelConfirmation() {
    this.isFirstStep = !this.isFirstStep;
    this.changeStep.emit(this.isFirstStep);
  }

  constructor() { }

  ngOnInit() {
  }

}
