import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MeetingRoomReservationPageComponent } from './components/meeting-room-reservation-page/meeting-room-reservation-page.component';
import { BookingMasterPageComponent } from '../booking-master-page/booking-master-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';
import { CompanyWebResolver } from '../services/companies/company-web.resolver';

const routes: Routes = [
  {
    path: 'root',
    component: MeetingRoomReservationPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Бронирования переговорных' }
  },
  {
    path: ':id',
    component: MeetingRoomReservationPageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver,
    },
    data: { animation: 'fadeAnimation', title: 'Бронирования переговорных' }
  },
  {
    path: ':id/master',
    component: BookingMasterPageComponent,
    resolve: {
      webId: CompanyWebResolver,
    },
    data: { animation: 'fadeAnimation', title: 'Бронирование переговорных комнат' }
  },
  {
    path: '',
    redirectTo: 'root',
    pathMatch: 'full'
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [ GroupsResolver ],
    exports: [RouterModule]
})
export class BookingRoutingModule { }
